import { mapState } from 'vuex'
export default {
    name: 'commentItem',
    props: {
        commentData: {
            type: Object,
            default () {
                return {}
            }
        }
    },
    filters: {
        userText (val) {
            let temp1 = val.split('')
            let temp2 = val.split('')
            let val1 = temp1.splice(0, 3)
            let val2 = temp2.splice(7, 11)
            return `${val1.join('')}****${val2.join('')}`
        }
    },
    data () {
        return {
            showMenu: false
        }
    },
    computed: {
        ...mapState(['userInfo']),
        isSelf() {
            if (!this.userInfo) return false
            this.commentData.username === this.userInfo && this.userInfo.username
        }
    },
    methods: {
        changeShowMenu () {
            this.showMenu = !this.showMenu
        },        
        async deletComment () {
            let params = {
                id: this.commentData.id
            }
            const res = await this.$api.deletComment(params)
            if (res.code === '0') {                
                this.$tip.toast('删除成功','none')
            }
        },
    },
}
